</template>

<script>
import MyHeader from "@/components/Common/mobile/MyHeader";
import FansButton from "@/components/Common/pc/FansButton";
export default {
  name: "ToolsIndex",
  components: {MyHeader,FansButton},
  data(){
    return{
      routename:"",
    }
  },
  watch:{
    routename:{
      handler(val) {
        this.isShow=false
